export default {
	data() {
		return {
			transform: 'translateY(50vh)',
			timer: 0,
			backgroundColor: 'rgba(0,0,0,0)',
			show: false,
			config: {},
		};
	},
	props:{
		contentHeight:{
			type: Number,
			default: 0
		},
		//是否是tabbar页面
		hasTabbar:{
			type: Boolean,
			default: false
		},
		shareList:{
			type: Array,
			default: function(){
				return [];
			}
		}
	},
	created() {
		const height = uni.upx2px(this.contentHeight) + 'px';
		this.config = {
			height: height,
			transform: `translateY(${height})`,
			backgroundColor: 'rgba(0,0,0,.4)',
		}
		this.transform = this.config.transform;
	},
	methods:{
		toggleMask(){
			//防止高频点击
			if(this.timer == 1){
				return;
			}
			this.timer = 1;
			setTimeout(()=>{
				this.timer = 0;
			}, 500)
			
			if(this.show){
				this.transform = this.config.transform;
				this.backgroundColor = 'rgba(0,0,0,0)';
				setTimeout(()=>{
					this.show = false;
					this.hasTabbar && uni.showTabBar();
				}, 200)
				return;
			}
			
			this.show = true;
			//等待mask重绘完成执行
			if(this.hasTabbar){
				uni.hideTabBar({
					success: () => {
						setTimeout(()=>{
							this.backgroundColor = this.config.backgroundColor;
							this.transform = 'translateY(0px)';
						}, 10)
					}
				});
			}else{
				setTimeout(()=>{
					this.backgroundColor = this.config.backgroundColor;
					this.transform = 'translateY(0px)';
				}, 10)
			}
		},
		//防止冒泡和滚动穿透
		stopPrevent(){},
		//分享操作
		shareToFriend(provider){
			let scene='WXSceneSession'
			this.$api.msg(`分享给${provider}`);
			if (provider === 'weixin2') {
				provider = 'weixin'
				scene = 'WXSenceTimeline'
			}
			
			this.toggleMask();
			let data = {
				provider,
				href: "http://192.168.199.117:8080/#/myInfo/product/product?id=93", // 点击回来的链接
				title: "商店分享",
				 summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
				imageUrl:'http://img13.360buyimg.com/popWaterMark/jfs/t865/120/206320620/138889/dcc94caa/550acedcN613e2a9d.jpg'
			}
			this.$api.shopShare(data,scene)
			
		},
		
		// 分享
		shopShare(grenr){
			console.log(grenr)
			uni.share({
				provider:grenr,
				scene:'WXSceneSession',
				type:0,
				href: "http://192.168.199.117:8080/#/myInfo/product/product?id=93", // 点击回来的链接
				 title: "商店分享",
				 summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
				imageUrl:'http://img13.360buyimg.com/popWaterMark/jfs/t865/120/206320620/138889/dcc94caa/550acedcN613e2a9d.jpg',
				success: (res) => {
					console.log('分享成功',res)
				},
				fail: (err) => {
					console.log('分享失败：',err)
				}
				
			})
			
		}
	}
}
